main {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.cat-info {
  text-align: center;
  cursor: pointer;
  border-radius: 10px;
  justify-content: center;
  max-width: 600px;
  margin: 50px auto 0;
  padding: 20px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 10px 30px 5px #0003;
}

.cat-img {
  border-radius: 10px;
  width: 100%;
  height: 100%;
  margin-bottom: 20px;
  top: 0;
  left: 0;
}

.cat-name {
  text-transform: uppercase;
  margin: 0;
  font-family: Roboto Condensed, sans-serif;
  font-weight: normal;
}

.cat-description {
  font-family: Roboto Condensed, sans-serif;
  font-weight: normal;
}

.cat-temperament {
  color: #9daeae;
  inset: auto auto 80px 30px;
}

.loader {
  color: #ff3d00;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  animation: 1s ease-in infinite alternate fill;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  width: 100%;
  height: 100%;
  animation: .9s ease-in infinite alternate fill;
  position: absolute;
  top: 0;
  left: 48px;
}

.loader:after {
  animation-duration: 1.1s;
  left: auto;
  right: 48px;
}

@keyframes fill {
  0% {
    box-shadow: inset 0 0 0 2px;
  }

  100% {
    box-shadow: inset 0 0 0 10px;
  }
}

